@import url(https://fonts.googleapis.com/css2?family=Lexend+Mega&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
* {
  font-family: 'Lexend Mega', sans-serif;
}

html,
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  background: #ffffff;
  color: #606063;
  overflow: hidden;
  font-family: 'Lexend Mega', sans-serif;
  text-align: center;
}

p {
  font-size: 0.9rem;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.canvas-wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
}

.scrollArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.info-card {
  position: absolute;
  text-align: center;
  max-width: 500px;
  max-height: 500px;
  padding: 3em;
  color: #000000;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
}



.qr-code {
  width: 200px;
  height: 200px;
}

@media only screen and (max-width: 600px){
  .qr-code {

  }
}
*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;font-size:14px}#root{overflow:auto}body{position:fixed;overflow:hidden;overscroll-behavior-y:none;font-family:-apple-system,BlinkMacSystemFont,avenir next,avenir,helvetica neue,helvetica,ubuntu,roboto,noto,segoe ui,arial,sans-serif;color:#000;-webkit-font-smoothing:antialiased;background:#fff}header{position:absolute;height:108px;width:100%;padding:24px 48px;display:flex;align-items:center;justify-content:space-between;color:#2a2a24}header .logo span,header .episode span{font-size:1rem;font-weight:700;letter-spacing:.04rem}
